export default {
  data() {
    return {
      showList: [],
      id: 0,
      keys: "",
      loading: false,
      ruleForm: {
        show: {},
        coins: "",
        fech: "",
        time: "",
        refresh_time: "",
        robot_change_rate: "",
        robot_change_min: "",
        robot_change_max: ""
      },
      rules: {
        coins: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        fech: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        refresh_time: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        robot_change_rate: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        robot_change_min: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ],
        robot_change_max: [
          { required: true, message: "必填项不可以为空", trigger: "blur" }
        ]
      }
    };
  },
  async created() {
    let { data } = await this.$http.HallFunConfig.Getrank_control({
      key: "rank_control.lua"
    });
    // console.log(data.data);
    let res = JSON.parse(data.data[0].sys_val);
    // console.log(res);
    this.id = data.data[0].id;
    this.keys = data.data[0].sys_key;
    this.ruleForm = res;

    Object.keys(res.show).forEach(key => {
      // console.log(key, res.show[key]);
      if (res.show[key] === "on") {
        this.showList.push(+key);
      }
    });
    // console.log(this.showList);
  },
  methods: {
    //发送到服务器配置
    async send() {
      // console.log("发送到服务器配置");
      // console.log(this.showList);
      this.loading = true;
      let showresl = {};
      this.showList.forEach(item => {
        showresl[item] = "on";
      });
      this.ruleForm.show = showresl;
      // console.log(this.ruleForm);

      let { data } = await this.$http.HallFunConfig.Postrank_control({
        keys: this.keys,
        id: this.id,
        values: JSON.stringify(this.ruleForm)
      });
      // console.log(data);

      if (data.code === 1 && data.msg === "ok") {
        this.loading = false;
        this.$message({
          type: "success",
          message: "配置到服务器成功!"
        });
      } else {
        this.loading = false;
        this.$message({
          type: "warning",
          message: "发送服务器配置失败"
        });
      }
    },

    //保存数据
    submitForm(formName) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          // console.log(this.showList);
          let showres = {};
          this.showList.forEach(item => {
            showres[item] = "on";
          });
          this.ruleForm.show = showres;
          let { data } = await this.$http.HallFunConfig.Putrank_control({
            keys: this.keys,
            id: this.id,
            values: JSON.stringify(this.ruleForm)
          });
          // console.log(data);
          if (data.code === 1 && data.msg === "ok") {
            this.$message({
              type: "success",
              message: "保存成功!"
            });
          } else {
            this.$message({
              type: "warning",
              message: "保存失败"
            });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  }
};



